/* General styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
  color: #333;
}

h1 {
  color: #4CAF50;
  text-align: center;
  margin-top: 2rem;
}

/* Container styles */
.todo-container {
  max-width: 600px;
  margin: 2rem auto;
  background: #fff;
  padding: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

/* Input and button styles */
input[type="text"] {
  width: calc(100% - 100px);
  padding: 0.8rem;
  margin-right: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  box-sizing: border-box;
}

input[type="text"]:focus {
  outline: none;
  border-color: #4CAF50;
  box-shadow: 0 0 5px rgba(76, 175, 80, 0.5);
}

button {
  padding: 0.8rem 1.5rem;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

button:active {
  background-color: #3e8e41;
}

/* Task list styles */
ul {
  list-style-type: none;
  padding: 0;
  margin-top: 1rem;
}

li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.8rem;
  background: #f9f9f9;
  border: 1px solid #ddd;
  margin-bottom: 0.5rem;
  border-radius: 5px;
}

li:hover {
  background-color: #f1f1f1;
}

/* Task text styles */
li span {
  cursor: pointer;
  font-size: 1rem;
  flex: 1;
}

li span.completed {
  text-decoration: line-through;
  color: #999;
}

/* Delete button styles */
li button {
  padding: 0.5rem 1rem;
  background-color: #ff4d4d;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

li button:hover {
  background-color: #e60000;
}
